body{
    background:#191641;
    margin:0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: black;
}

.todo-container{
    background:#BBC7C8;
    text-align: center;
    padding:10px;
    border-radius: 10px;
    margin: auto; 
    width: 30rem;
}

.input-section{
    display: flex;
    margin-top:10px;
}

.todo-container h1{
    pointer-events: none;
    padding: 0;
    font-size: 1.5rem;
    font-weight:bold  
}


.add-btn{
    margin-left: 1rem;
    background:#191641 ;
    border-radius: 3px;
    border: transparent;
    color: white;
    height: 2.5rem;
    width: 4rem;
    margin-top:0;
}

.todo-list{
    display: flex;
    padding: 10px;
    background:#BBC7C8;
    border-radius: 10px;
    align-items: center;
    justify-content: space-between;
    margin: auto;
    font-size: 15px;
    width: 30rem;
}

.delete-btn{
    border-radius: 3px;
    border: transparent;
    height: 2.5rem;
    width: 3.5rem;
    background:#191641;
    color: white;
}

.todo-list-completed{
    display: flex;
    padding: 10px;
    background:#4f5d75;
    border-radius: 10px;
    align-items: center;
    justify-content: space-between;
    margin: auto;
    font-size: 15px;
    width: 30rem;
}


.task{
    display: flex;
    padding-left: 10px;
}

.task input{
    width: 20px;
    height: 25px;
}

.main-container{
    margin-top: 40px;
}

.completed{
    text-transform: capitalize;
    margin: 0; 
    padding: 3px;
    padding-left: 10px;
    text-decoration: line-through;
    pointer-events: none;
}

.not-completed{
    text-transform: capitalize;
    margin: 0; 
    padding: 3px;
    padding-left: 10px;
    pointer-events: none;
}

.user-details{
    color: white;
    font-size: 1.5rem;
}

.login-icon{
    font-size: 1.5rem;
    cursor: pointer;
    color: white;
}

@media (max-width:500px) {
    .todo-container{
        width: 22rem;
    }
    .todo-list{
        width: 22rem;
    }
    .todo-list-completed{
        width: 22rem;
    }
}

